@import "https://fonts.googleapis.com/css?family=Exo:700";
@import "https://fonts.googleapis.com/css?family=Abel";
body {
  box-sizing: border-box;
  color: #bda2a2;
  background: linear-gradient(to right, #241f1f 0% 32%);
  font-family: Ysabeau SC, sans-serif;
  overflow-x: hidden;
}

.mainContainer {
  flex-direction: column;
  display: flex;
}

h1 {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 4rem;
}

.blogContainer {
  grid-template-columns: repeat(3, 1fr);
  grid-auto-flow: row;
  margin-top: 2rem;
  display: grid;
  overflow: scroll;
}

.blog-card {
  transform-style: preserve-3d;
  justify-content: center;
  display: flex;
  transform: perspective(900px);
}

a {
  text-decoration: none;
}

.inner-card {
  text-align: center;
  background-color: #241f1f;
  border-radius: 3%;
  width: 300px;
  height: 400px;
  margin-top: 10px;
  margin-bottom: 10px;
  transition: all .6s;
  transform: rotateX(65deg)translateY(-200px)translateZ(-100px);
  box-shadow: 0 20px 60px #00000080;
}

.inner-card:hover {
  transition: all .6s;
  transform: rotateZ(0);
  box-shadow: 0 0 20px #0000004d;
}

.inner-card img {
  width: 200px;
  height: 120px;
  transform: translateY(15px);
}

.inner-card h2 {
  color: #fff;
  text-shadow: 0 0 2px #fff;
  font-family: Abel, sans-serif;
  font-size: 25px;
  transform: translateY(10px);
}

.para {
  color: #fff;
  width: 220px;
  font-family: Abel, sans-serif;
  transform: translateX(40px);
}

@media (width <= 700px) {
  .blogContainer {
    grid-template-rows: repeat(3, 1fr);
    grid-template-columns: 1fr;
    gap: 0;
    margin-top: 2rem;
    display: grid;
  }

  .card {
    justify-content: center;
    display: flex;
  }
}

nav {
  z-index: 10;
  position: sticky;
  top: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo {
  width: 10rem;
  height: 3rem;
  padding-top: 20px;
}

#navDiv > ul {
  gap: 1rem;
  display: inline-flex;
}

.nav-item {
  letter-spacing: 1.5px;
  list-style: none;
  transition: transform .2s;
}

li > a {
  color: #bda2a2;
  text-decoration: none;
}

ul > .nav-item a:not(.active):hover {
  cursor: default;
  background-image: linear-gradient(#0000 0 55%, #a45959 55% 90%, #0000 90% 100%);
  background-repeat: no-repeat;
  background-size: 0% 90%;
  animation: .5s cubic-bezier(.645, .045, .355, 1) .1s forwards underline1;
}

@keyframes underline1 {
  100% {
    background-size: 7rem 90%;
  }
}

.active {
  background-image: linear-gradient(#0000 0 55%, #a45959 55% 90%, #0000 90% 100%);
  background-repeat: no-repeat;
  background-size: 0% 110%;
  animation: .5s cubic-bezier(.645, .045, .355, 1) .1s forwards underline;
}

@keyframes underline {
  100% {
    background-size: 7rem 110%;
  }
}

nav div.menu {
  display: none;
}

@media (width <= 700px) {
  nav div.menu {
    display: flex;
  }

  #navDiv > ul {
    display: none;
  }

  #navDiv > ul.open {
    z-index: 5;
    flex-direction: column;
    width: 100%;
    margin-bottom: .25rem;
    padding-left: 0;
    display: flex;
  }

  nav ul li {
    text-align: center;
    width: 100%;
  }

  nav ul li a {
    margin: .2rem, .5rem;
  }

  .btn {
    width: 1.5rem;
    height: 1.5rem;
  }
}

